:root {
    scroll-behavior: smooth;
}

::selection {
    color: white;
    background: #82522F;
}

body {
    max-width: 100vw;
    z-index: 99;
    cursor: none;
}

a {
    cursor: none;
}

summary {
    margin-bottom: 0.5rem;
    margin-top: 0.5rem;

    &:focus {
        outline: none;
    }
}

details {
    &:focus {
        outline: none;
    }

    // cursor: pointer;
}

ul {
    width: 100%;
    margin-bottom: 3rem;

    li {
        padding: 1rem 1rem 1rem 0rem;
        width: 100%;
        border-bottom: solid #d9ddce;
    }
}

.is-fullheight {
    min-height: 75vh;
}

.is-playfair {
    font-family: 'Playfair Display', serif;
}

.is-manrope {
    // font-family: 'Manrope', sans-serif;
    font-family: 'Noto Sans JP', sans-serif;
    opacity: 0.8;
}

.is-dark-brown {
    color: #82522F !important;
}

.is-light-brown {
    color: #fff !important;
}

.center {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.underline {
    width: 100%;
    position: relative;
    text-decoration: none;

    &:before {
        content: "";
        position: absolute;
        width: 100%;
        height: 2px;
        bottom: 0;
        left: 0;
        background-color: #82522F;
        visibility: hidden;
        transform: scaleX(0);
        transition: all 0.3s ease-in-out 0s;
    }

    &:hover:before {
        visibility: visible;
        transform: scaleX(1);
    }
}

.image-stack {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    position: relative;
}

.image-stack__item--top-1 {
    grid-column: 1 / span 8;
    grid-row: 1; // must be on the same row as the other image
    padding-top: 20%; // this pushes the image down, and keeps it proportional as it resizes
    z-index: 999; // make this image render on top of the bottom
}

.image-stack__item--top-2 {
    grid-column: 1 / span 8;
    grid-row: 1; // must be on the same row as the other image
    padding-top: 70%; // this pushes the image down, and keeps it proportional as it resizes
    z-index: 999; // make this image render on top of the bottom
}

.image-stack__item--bottom {
    grid-column: 4 / -1;
    grid-row: 1; // make this image be on the same row
    z-index: 998;
    // will-change: transform;
}


.hero:before {
    animation: grain 8s steps(10) infinite;
    background-image: url("/static/shirt.webp");
    content: "";
    height: 300%;
    left: -50%;
    opacity: 0.15;
    position: fixed;
    top: -125%;
    width: 300%;
    pointer-events: none;
    z-index: 0;
}

@keyframes grain {

    0%,
    100% {
        transform: translate(0, 0)
    }

    10% {
        transform: translate(-5%, -10%)
    }

    20% {
        transform: translate(-15%, 5%)
    }

    30% {
        transform: translate(7%, -25%)
    }

    40% {
        transform: translate(-5%, 25%)
    }

    50% {
        transform: translate(-15%, 10%)
    }

    60% {
        transform: translate(15%, 0%)
    }

    70% {
        transform: translate(0%, 15%)
    }

    80% {
        transform: translate(3%, 35%)
    }

    90% {
        transform: translate(-10%, 10%)
    }
}

.contact {
    h1 {
        margin-bottom: 1rem;
        font-weight: bold;
    }

    ul {
        padding-bottom: 3rem;

        li {
            border: none;
            color: #d9ddce;
            padding: 0.5rem 0 0 0;
        }
    }
}

.left-hint {
    position: absolute;
    left: 1.5rem;
    top: 80vh;
    writing-mode: vertical-lr;
    opacity: 0.5;
    transform: translateY(-100%) rotateZ(180deg);
    user-select: none;
    // cursor: default;
}

.right-plus {
    position: absolute;
    // cursor: default;
    opacity: 0.5;
    left: 1rem;
    user-select: none;
    top: 0rem;
}

#crsr {
    display: block;
    opacity: 0;
    max-height: 180px;
    max-width: 320px;
    z-index: 999;
    position: fixed;
    pointer-events: none;
}

.imgcursor {
    transition: opacity 0.5s ease-in-out;
}

.visible {
    opacity: 1 !important;
    pointer-events: none;
}

.herostuff {
    transform: translateY(110%);
    opacity: 0;
}

.herodeco {
    opacity: 0
}

.text-wrap {
    overflow: hidden;
    display: flex;
    align-items: flex-start;
    margin-right: 0.5rem;

    .text-element {
        display: inline-block;
        transform: translate3d(0, 110%, 0);
    }
}

.text-container {
    display: flex;
    justify-content: flex-start;
}

.bg-text {
    opacity: 0.1;
    font-size: 10vw;
    font-weight: bold;
    margin: 3rem 0;
    // cursor: default;
    user-select: none;
}


.tags {
    display: flex;
    justify-content: flex-start;

    .tag {
        padding: 0 1rem 0 0;
        font-style: italic;
        opacity: 0.5;
    }
}

.project-desc {
    p {
        margin: 1rem 0;
    }
}

@media screen and (min-width: 768px) {
    .project-desc {
        p {
            max-width: 50%;
            transform: translateX(50%);
        }

        .tags {
            justify-content: center;
        }
    }
}

.email-cta {
    transition: color 0.25s ease-in;
    animation: email 1s linear infinite reverse;
}

@keyframes email {
    0% {
        color: #B4641C;
    }

    100% {
        color: #B89E5F;
    }
}


// cursor stuff
:root {
    --x: 0px;
    --y: 0px;
    --cSizeSmall: 14px;
}

.cursor {
    position: fixed;
    top: 0;
    left: 0;
    background-color: #1C04FC;
    border-radius: 50%;
    will-change: transform;
    transform-style: preserve-3d;
    z-index: 9999;
    pointer-events: none;
    width: var(--cSizeSmall);
    height: var(--cSizeSmall);
    transform: translate3d(calc(var(--x) - var(--cSizeSmall)/2), calc(var(--y) - var(--cSizeSmall)/2), 1);
    transition: width 0.5s ease-in-out, height 0.5s ease-in-out, opacity 1s ease-out, background-color 1s ease-in-out;
}

.preloader-name {
    display: inline-block;
}

.cursor-hover {
    width: 64px;
    height: 64px;
    opacity: 0.35;
}